@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Helvetica, sans-serif, Arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}

.Navbar {
  /* position: relative; */
  z-index: 100;
  width: 100%;
  background-color: #02203c !important;
  border-right: 15px solid #02203c;
  border-left: 15px solid #02203c;
}

.Navbar .Logo {
  height: 40px;
}

.Navbar .NavbarBrand {
  font-size: 30px;
  font-family: "Permanent Marker";
}

.navbar-collapse.show .ml-auto.navbar-nav button {
  width: 100% !important;
  background-color: #15314b;
  align-items: center;
  border: none;
}

.navbar-collapse.show .ml-auto.navbar-nav .dropdown-item {
  align-items: center;
}

.Navbar .btn-primary {
  color: #fff;
  background-color: #15314b !important;
  border-color: rgba(0, 0, 0, 0.1) !important;
  font-weight: 900;
  margin-right: 20px;
}

.languageBtn .btn-primary {
  width: 350px !important;
}

.fontSizeBtn .btn-primary {
  width: 150px !important;
}

.share {
  width: 50px !important;
}

.btn-primary:hover {
  color: #fff;
  background-color: #626ee3 !important;
  border-color: rgba(0, 0, 0, 0.1) !important;
}

.dropdown-menu {
  background: #15314b;
}

.dropdown-menu a {
  color: #ffffff;
}

.dropdown-menu a:hover {
  background-color: #626ee3;
}

.dropdown-menu .selected {
  background-color: #e0e0e0;
  color: #15314b;
}

.IdeEditor {
  width: 100%;
  height: 100%;
  border-left: 15px solid #02203c;
  border-bottom: 15px solid #02203c !important;
}

.ace_editor {
  background: #001528 !important;
  height: 95% !important;
}
.ace_gutter {
  background: #001528 !important;
  z-index: 0;
}

.IdeEditor {
  position: relative;
  background: #15314b;
}

.ace_comment {
  color: #8b8888 !important;
}

.IdeEditor .EditorNav {
  display: flex;
  justify-content: space-between;
  margin: 0;
  text-decoration: none;
  list-style: none;
}

.IdeEditor ul li p {
  color: #ffffff;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding: 5px 20px;
  background-color: #001528;
  height: 2rem;
  margin: 0;
}

.IdeEditor button {
  height: 1.65rem;
  width: 4rem;
  margin-top: 5px;
  margin-right: 7px;
  outline: none !important;
  box-shadow: none !important;
  border-radius: 0;
}

.IdeInput {
  background: #15314b;
  height: 100%;
  width: 100%;
  border-right: 15px solid #02203c;
}

.IdeInput p {
  color: #ffffff;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding: 5px 20px;
  background-color: #001528;
  height: 2rem;
  margin: 0;
}

.IdeInput textarea {
  /* Firefox */
  /* WebKit */
  /* Opera */
  height: -o-calc(100% - 2rem);
  /* Standard */
  height: calc(100% - 2rem);
  width: 100%;
  font-family: Consolas, Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif !important;
  padding: 20px 30px;
  background: #001528;
  color: #ffffff;
  border: none;
  overflow: auto;
  outline: none;
  box-shadow: none;
  resize: none;
}

.IdeOutput {
  background: #15314b;
  height: 100%;
  width: 100%;
  border-right: 15px solid #02203c;
  border-bottom: 15px solid #02203c;
}

.IdeOutput p {
  color: #ffffff;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding: 5px 20px;
  background-color: #001528;
  height: 2rem;
  margin: 0;
}

.output {
  height: 100%;
  background: #001528;
}

.spinner {
  background: #001528;
}

.IdeOutput textarea {
  /* Firefox */
  /* WebKit */
  /* Opera */
  height: -o-calc(100% - 2rem);
  /* Standard */
  height: calc(100% - 2rem);
  width: 100%;
  font-family: Consolas, Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif !important;
  overflow: hidden;
  padding: 20px 30px;
  background: #001528;
  color: #ffffff;
  border: none;
  overflow: auto;
  outline: none;
  box-shadow: none;
  resize: none;
}
